.cb-syh-page-no-risk {
	/* margin-top: -100px; */
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	width: 100%;
	padding: 0px 15px 0;
	max-width: 1080px;
	background-color: #fff;

	position: relative;
	z-index: 10;
}
.cb-syh-page-no-risk ul{
		display: block;
		list-style-type: disc;
		margin-block-start: 0;
		margin-block-end: 20px;
		margin-inline-start: 0px;
		margin-inline-end: 0px;
		padding-inline-start: 40px;
		unicode-bidi: isolate;
  
}
.cb-syh-page-no-risk li{
		/* font-family: "Poppins", sans-serif !important; */
  font-size: 16px;
  line-height: 1.5;
  font-weight: 400;
  color: #1d1d1d;
  text-align: left;
}
.cb-syh-page-no-risk li a{
text-decoration: none;
color: #0c91b2;
}


.cb-syh-page-no-risk h2 {
	margin: 0;
	text-align: center;
	margin: 30px auto 20px;
	line-height: 1.2;
	color: #212529;
	font-size: 35px;
	font-weight: 700;
	/* font-family: "Poppins", sans-serif !important; */
}

.cb-syh-page-no-risk p {
	/* font-family: "Poppins", sans-serif !important; */
	font-size: 24px;
	font-weight: 400;
	/* text-align: center; */
	line-height: 1.5;
	color: #212529;
text-align: left;
	margin: 0 0 20px;
	text-align: left;
}
.cb-syh-page-no-risk p a{
text-decoration: none;
color: #02bdfc;
}
@media (min-width: 1024px) {
	.cb-syh-page-no-risk {
		padding: 0px 30px 0;
	}
	.cb-syh-page-no-risk h2 {
		font-size: 40px;
	}
	.cb-syh-page-no-risk  p {
		font-size: 24px;
	}
	.cb-syh-page-no-risk li{
  font-size: 18px;
}
}


